<template>
    <div class="page-index">
        <section class="main" v-if="data.length && $route.path == '/specialtopic/index'">
            <van-list
                v-if="data.length"
                v-model="loading"
                :finished="finished"
                finished-text=""
                @load="onLoad"
            >
                <ul class="list">
                    <li  v-for="(item,key) in data" :key="key"  @click="handleToList(item)" >
                        <div class="flex item-box">
                            <div class="left">
                                <img :src="item.pic" >
                            </div>
                            <div class="right">
                                <p class="name">{{item.name}}</p>
                                <p class="description">{{item.description}}</p>
                                <div class="other flex">
                                    <p class="time">{{item.dateline | timeformat()}}</p>
                                    <div class="views">{{item.views}}</div>
                                    <div class="replies">{{item.replies}}</div>
                                </div>
                            </div>
                        </div>
                        <van-divider />
                    </li>
                </ul>
            </van-list>
        </section>
        <router-view :itemdata='itemdata'></router-view>
    </div>
</template>

<script>
import { Toast } from 'vant';
import {getFromNom} from '@js/moment'
export default {
    data(){
        return {
            data:[],
            itemdata:'',
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            },
        }
    },
    created(){
        this.getSquareClass();
    },
    methods:{
        onLoad(){
            this.getSquareClass();
        },
        handleToList(item){
            this.itemdata = item;
            let url = `${this.$store.state.indexUrl+'specialtopic/list?typeid='+item.typeid+'&pic='+item.pic+'&views='+item.views+'&replies='+item.replies+'&'+this.$store.state.appParams}`,
                json = {url:url,share:'0'};
 
            window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)    
           // this.$router.push({path:'/specialtopic/list',query:{typeid:item.typeid,pic:item.pic,views:item.views,replies:item.replies,name:item.name}})
        },
        getSquareClass(){
            // Toast.loading({
            //     message: '加载中...',
            //     forbidClick: true
            // });
            this.api.getSquareClass({
                token:this.$store.state.userData,
                fid:130,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then( res => {
                //Toast.clear();
                this.loading = false;
                if(res.err_code == 0){
                    if(!res.data.threadclass.length){
                        this.finished = true;
                    }
                    if(this.pageInfo.page_index == 0){
                        this.data = [];
                    }
                    this.data = [...this.data,...res.data.threadclass]
                    this.pageInfo.page_index++;
                }
            })
        },
    },
    filters:{
        timeformat(v){
        if(v && v != 0){
            return getFromNom(v*1000)
        }else {
            return ''
        }
        }
    }    
}
</script>

